<template>
  <div class="q-music">
      <div class="q-nav">
        <ul class="clear-fix">
          <li>
            <router-link to="/qmusic/home"></router-link>
          </li>
          <li>
            <router-link to="/qmusic/singer"></router-link>
          </li>
          <li>
            <router-link to="/qmusic/album"></router-link>
          </li>
          <li>
            <router-link to="/qmusic/toplist"></router-link>
          </li>
          <li>
            <router-link to="/qmusic/search"></router-link>
          </li>
          <li>
            <router-link to="/qmusic/playlist"></router-link>
          </li>
          <li>
            <router-link to="/qmusic/radio"></router-link>
          </li>
          <li>
            <router-link to="/qmusic/mv">MV</router-link>
          </li>
          <li>
            <router-link to="/qmusic/digital"></router-link>
          </li>
          <li>
            <router-link to="/qmusic/mymusic"></router-link>
          </li>
        </ul>
      </div>
      <router-view></router-view>
  </div>
</template>

<script>
  export default{
    mounted(){
      if(this.$router.currentRoute.path=='/qmusic'){
        this.$router.push('/qmusic/singer')
      }
    },
    watch:{
      $route(to){
        if(to.path=='/qmusic'){
          this.$router.push('/qmusic/singer')
        }
      }
    }
  }
</script>

<style>
  #app .q-music ul{
    text-align: left;
  }
  #app .q-music{
    position: relative;
  }
  #app .q-music li{
    width: 80px;
    float: left;
    color: #42b983;
    text-align: left;
  }
  #app .q-music .q-nav{
    position: relative;
    left: -150px;
    text-align: left;
  }
  #app .q-music .q-nav li{
    text-align: center;
    font-family: -webkit-body
  }
  .q-music li a{
    color: #42b983;
  }
</style>



